import React from 'react';
import { Link } from 'react-router-dom';
import { useEffect } from "react";
import { useLocation } from "react-router-dom";

const MyReactPortfolioRM = () => {

    const { pathname } = useLocation();

    useEffect(() => {
        window.scrollTo(0, 0);
    }, [pathname]);

    return (
        <div className="ProjectRMContainer">
            <div className="ProjectRMTab">
                <Link to="/MyReactPortfolio">Home</Link>
            </div>
            <div className='ProjectRMContentContainer'>
                <div className="ProjectRMContent">
                    <h1>MyReactPorfolio</h1>
                    <hr />
                </div>
                <div className="ProjectRMContent">
                    <h3 className="semititle">  </h3>
                    <p>   <strong>  </strong> </p>
                    <h3>*  </h3>
                    <ul>
                        <li>react-reveal  Fade</li>
                        <li>react-scroll     </li>
                    </ul>
                </div>

                <div className="ProjectRMContent">
                    <h3>&nbsp; </h3>
                    <p>        .</p>
                    <p>   react-reveal Fade      .</p>
                    <p>
                        react-reveal              .
                          spy         .
                    </p>
                    <p>
                        window.addEventListener  scroll   , Fade spy 
                        Fade    .
                    </p>
                    <p>
                             react-scroll                   .
                    </p>
                    <p>    throttle  0.1        .</p>
                </div>
                <div className="ProjectRMContent">
                    <h3>  </h3>
                    <p>             .</p>
                </div>
            </div>
            <div className="ProjectRMFooter">

            </div>
        </div>
    );
};

export default MyReactPortfolioRM;